<template>
<div>
    <v-btn fixed dark fab bottom right color="indigo" @click="this.onSave" id="btn-download">
        <v-icon>mdi-download</v-icon>
    </v-btn>

    <v-layout justify-center wrap>
        <v-flex xs12 md6 pa-5>
            <input type="file" id="imageInput" accept="image/*">
              <div class="top">

<v-card class="container " id="my-node" >
                <canvas id="myCanvas" width="100%" height="100%"></canvas>
                <div class="secondImage">
                  <img :src="rlogo" alt="" width="100%" />
                </div>
                
            </v-card>

              </div>
            

            <v-file-input type="file" accept="image/png, image/jpeg, image/bmp" placeholder="Upload a Photo" prepend-icon="mdi-upload" label="Upload Photo" @change="this.onImageChange" :clearable="false" v-model="images" show-size></v-file-input>
            <center>
                or
            </center>
            <v-file-input class="hidden-md-and-up" type="file" accept="image/*" placeholder="Pick a Photo" prepend-icon="mdi-camera" label="Take Photo" capture="" @change="this.onImageChange" :clearable="false" v-model="images" show-size></v-file-input>
        </v-flex>
        <v-flex xs12 md6 pa-5>
            <div id="my-node-">
                <v-img :src="imageUrl" width="100%" height="auto" alt="Alternative" class="firstImage">
                    <v-img :src="rlogo" alt="" width="100%" class="secondImage" />
                </v-img>
            </div>
        </v-flex>
    </v-layout>
    <div>
        <div id="my-node">
            <v-img :src="imageUrl" width="100%" height="auto" alt="Alternative" class="firstImage" id="my-img">
                <v-img :src="rlogo" alt="" width="100%" class="secondImage" />
            </v-img>
        </div>
    </div>

</div>
</template>

<script>
import domtoimage from 'dom-to-image';
import rlogo from "@/assets/rlogo.png"

export default {
    name: 'Camera',
    data() {
        return {
            imageUrl: null,
            rlogo: rlogo,
            images: null,
            exampleContent: ""
        }
    },

    mounted() {
        var canvas = document.getElementById("myCanvas");
        console.log(canvas);
        let imgInput = document.getElementById('imageInput');
        imgInput.addEventListener('change', function (e) {
            if (e.target.files) {
                let imageFile = e.target.files[0]; //here we get the image file
                var reader = new FileReader();
                reader.readAsDataURL(imageFile);
                reader.onloadend = function (e) {
                    var myImage = new Image(); // Creates image object
                    myImage.src = e.target.result; // Assigns converted image to image object
                    myImage.onload = function () {
                        var myCanvas = document.getElementById("myCanvas"); // Creates a canvas object
                        var myContext = myCanvas.getContext("2d"); // Creates a contect object
                        myCanvas.width = myImage.width; // Assigns image's width to canvas
                        myCanvas.height = myImage.height; // Assigns image's height to canvas
                        myContext.drawImage(myImage, 0, 0); // Draws the image on canvas
                        // let imgData = myCanvas.toDataURL("image/jpeg",0.75); // Assigns image base64 string in jpeg format to a variable
                    }
                }
            }
        });
    },
    directives: {
        insertMessage: function (canvasElement, binding) {
            // Get canvas context
            var ctx = canvasElement.getContext("2d");
            // Clear the canvas
            ctx.clearRect(0, 0, 300, 150);

            ctx.drawImage(URL.createObjectURL(binding.value), 10, 50);
        }
    },
    watch: {
        exampleContent: function () {
            console.log("exampleContent changed to " + this.exampleContent);
        }
    },
    methods: {
        imageSave() {
            var elephant = document.getElementById("my-img");
            console.log("i askjd");

            // Take action when the image has loaded

            var imgCanvas = document.createElement("canvas"),
                imgContext = imgCanvas.getContext("2d");

            // Make sure canvas is as big as the picture
            imgCanvas.width = elephant.width;
            imgCanvas.height = elephant.height;

            // Draw image into canvas element
            imgContext.drawImage(elephant, 0, 0, elephant.width, elephant.height);

            // Get canvas contents as a data URL
            var imgAsDataURL = imgCanvas.toDataURL("image/png");

            // Save image into localStorage
            try {
                localStorage.setItem("elephant", imgAsDataURL);
            } catch (e) {
                console.log("Storage failed: " + e);
            }

        },
        onSave() {
            // var button = document.getElementById('btn-download');
            var canvas = document.getElementById("myCanvas");
            console.log(canvas);
            //   var dataURL = canvas.toDataURL('image/png');

            //   var link = document.createElement('a');
            // link.download = 'filename.png';
            // link.href = dataURL
            // link.click();

            var FileSaver = require('file-saver');
            var node = document.getElementById('my-node');
            var today = new Date();
            var name = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate() + today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();

            //        domtoimage.toPng(node)
            // .then(dataUrl => {
            //   const link = document.createElement('a');
            //   link.download = 'image.png';
            //   link.href = dataUrl;
            //   link.click();
            // })
            domtoimage.toBlob(node)
                .then(function (blob) {
                    FileSaver.saveAs(blob, name + 'Rotaract-Club-of-Majestic-CBE.png');
                }).catch(function (error) {
                    console.error('oops, something went wrong!', error);
                });
        },
        onImageChange() {
            this.imageUrl = URL.createObjectURL(this.images)

        }
    },
};
</script>

<style scoped>
.secondImage {
  height: inherit;
  width: inherit;
    max-height: inherit;
    min-height: inherit;
    position: absolute;
    bottom: 20px;
    z-index: 99;
    justify-self: center;
    min-width: inherit;
    max-width: inherit;
    background: red;
}

.container {
    position: relative;
    height: auto;
    width: auto;
    display: table;
}

.top{
  overflow: auto;
  width: 2px;
  height: 2px;
}

</style>
